.container-inicio{
    width: 100%;
    display: flex;
    margin-top: 5em;
}
.container-words{
    width: 35em;
    height: 20em;
    text-align: center;
    background: transparent;
    margin-left: auto;
    margin-right: auto;
    background-image: linear-gradient(-60deg, #ff585841 0%, #f0961973 100%);
    border-radius: 0.5em;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}
.container-words p{
    margin: 2em;
    font-family: 'Lilita One', cursive;
    display: flex;
    justify-content: center;
    margin-top: 3.5em;
    font-size: 1.7rem;
    color: black;
    text-shadow: 1px 0.1px rgb(180, 50, 50);
}
@media screen and (max-width:480px){
    .container-words{
        width: 20em;
        height: 30em;
    }
}