.container-card-fav{
    width: 100%;
    display: flex;
    flex-direction: column;
}
.container-card-fav h1{
    font-family: 'Lilita One', cursive; 
    color: aliceblue;
    text-shadow: 2px 2px 5px black;
    text-align: center;
}
.container-cards-movie{
    width: 90%;
    display: flex;
    flex-direction: row;
    margin-left: auto;
    margin-right: auto;

}
.card-movie-fav{
    margin: 0.5em;
    display: flex;
    width: 14em;
    background: transparent;
    background-image: linear-gradient(-60deg, #ff585841 0%, #f0961973 100%);
    border-radius: 0.5em;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}
.content-card-fav{
    padding: 0;
    margin: 0.5em;
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.content-card-fav p{
    font-family: 'Lilita One', cursive;
    font-size: 1.2rem;
    margin-top: 0.1em;
    margin-bottom: 0.3em;
    text-shadow: 1px 1px 1px black;
}
.content-card-fav img{
    width: 8em;
    height: 12em;
    border-radius: 0.5em;
    cursor: pointer;
}
.content-card-fav a{
    text-decoration: none;
    color: rgb(121, 52, 52);
}
.erase{
    outline: none;
    border: none;
    border-radius: 0.5em;
    font-family: 'Lilita One', cursive;
    font-size: 1.2rem;
    margin-top: 0.5em;
    box-shadow: 2px 2px 5px black;
    cursor: pointer;
    padding: 0.2em;
}
@media screen and (max-width:480px){
    .container-cards-movie{
        flex-direction: column;
    }
    .card-movie-fav{
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
        width: 14em;   
    }
    .content-card-fav img{
        width: 10em;
        height: 14em;
    }
    .content-card-fav p{
        font-size: 2rem;
    }
    .erase{
        font-size: 1.5rem;
    }
}